.sidebar {
  width: 250px;
  height: 100vh;
  background-color: #f5f1ea; /* tono nude/beige */
  position: fixed;
  left: 0;
  top: 0;
  transition: all 0.3s ease;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  z-index: 1000;
  border: solid 1px red
}

.hamburger-btn {
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
  margin-bottom: 20px;
}

.sidebar a {
  color: #4a3f35;
  text-decoration: none;
  font-weight: bold;
}

.sidebar a:hover {
  text-decoration: underline;
}
